.catalog {
  &__products {
    display: flex;
    flex-wrap: wrap;
    
    box-sizing: border-box;
    max-width: 1024px;
    margin: 0 auto;
    padding: 50px 40px;
  }

  &__card {
    margin-right: 48px;
    margin-bottom: 46px;

    &:nth-child(4n) {
      margin-right: 0;
    }

    &:nth-last-child(-n + 4) {
      margin-bottom: 0;
    }
  }
}
